body {
    background-color: #fff;
    height: 100vh;
    width: 100vw;
    margin: 0;
    padding: 0;
    outline: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}

div:focus {
    outline-color: #3e7327;
    outline-style: dashed;
}

/* This 'containerEunit' class literally does nothing; but leave it there! */
.containerEunit {
    background-color: inherit;
}

.App {}

.botIcon {
    position: fixed;
    left: -200px;
    top: 180px;
    width: 400px;
}

.botOutputsContainer {
    position: fixed;
    top: 15%;
    left: 25%;
    right: 5%;
    padding: 0;
    margin: 0;
    width: 100%;

}

.chatbotOutput {
    background-color: rgba(255, 255, 255, 0.7);
    box-shadow: 2px 2px 3px 0 rgba(0, 0, 0, 0.25);
    text-align: left;
    font-family: Ubuntu, sans-serif;
    border: 0.1pt solid lightgray;
    position: relative;
    justify-content: left;
    font-size: 20px;
}

/* Unrecognized devices START */

.chatbotOutput {
    min-width: 600px;
    width: 38%;
    max-width: 800px;
    line-height: 36px;
    border-radius: 10px;
    padding: 45px;
    display: inline-block;
    margin: 0 0 14px 0;
}


.quickReplyContainer {
    display: flex;
    margin: 10px 0;
    position: absolute;
    left: 25%;
    bottom: 20%;
    width: 60vw;
}

.quickReply {
    font-size: 17px;
    line-height: 24px;
    height: 26px;
    margin: 8px;
    border-radius: 12px;
    margin-top: 20px;
    padding: 8px 12px;
}

/* Unrecognized devices END */

.quickReplyContainer {
    word-wrap: normal;
    white-space: nowrap;
    overflow-x: auto;
    cursor: pointer;
}

.quickReplyContainer::-webkit-scrollbar {
    width: 8px;
    height: 12px;
    margin-top: 0;
}

.quickReplyContainer::-webkit-scrollbar-thumb {
    background: #f5f5f5;
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgb(165, 163, 163);
    box-shadow: inset 0 0 6px rgb(165, 163, 163);
}

/* Scroll bar Track */

/* .quickReplyContainer::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgb(165, 163, 163);
    box-shadow: inset 0 0 6px rgb(165, 163, 163);
} */

.quickReply {
    text-align: center;
    font-family: Ubuntu, sans-serif;
    text-decoration: none;
    text-transform: capitalize;
    outline: none;
    display: inline-block;
    cursor: pointer;
    font-style: normal;
    font-weight: 400;
    color: rgb(0, 0, 0);
    background-color: rgb(114, 179, 82, 0.2);
    border: 1px solid #f5f5f5;
    border-image: initial;

}

.quickReply:hover {
    background-color: rgb(114, 179, 82, 0.7);
}

.inputFieldContainer {
    position: fixed;
    margin-left: 5%;
    margin-right: 5%;
    max-width: 800px;
    width: 70%;
    left: 20%;
    bottom: 8%;
}

.inputField {
    z-index: 9999;
    font-family: Ubuntu, sans-serif;
    font-size: large;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.9);
    box-shadow: 12px 12px 16px 0 rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    display: flex;
    justify-content: center;
    margin-right: 4rem;
    padding-left: 15px;
    width: 100%;
    height: 50px;
}

input {
    padding: 5px;
}

button {
    z-index: 9999;
    position: absolute;
    right: -10px;
    top: 7px;
    width: 70px;
    height: 45px;
    -moz-appearance: none;
    border: none;
    background: #ededed;
    border-radius: 3px;
    padding: 10px;
    margin-top: 2px;
    text-transform: capitalize;
    transition: all .2s;
}

button:hover {
    background-color: #67AB67;
    color: white;
}

button:active {
    background-color: #468546;
    color: white;
}

.undoIcon {
    font-size: 1.5em;
    margin-top: 0.5em;
    margin-right: 1em;
    color:gray;
    cursor: pointer;
}

@media only screen and (max-width: 1024px) {
    .botOutputsContainer {
        position: fixed;
        top: 15%;
        left: 15%;
        right: 5%;
        padding: 0;
        margin: 0;
        width: 100%;
    }

    .chatbotOutput {
        max-width: 80%;
    }

    .inputFieldContainer {
        width: 80%;
        left: 0;
        margin-left: 5%;
        margin-right: 5%;
    }
}

/* Smaller Smarthphones */
@media only screen and (max-width: 480px) {

    .chatbotOutput {
        min-width: 250px;
        margin-bottom: 3%;
        line-height: 20px;
        padding: 14px 28px;
        display: inline-block;
        margin: 0 14px 14px 0;
    }

    .botOutputsContainer {
        position: fixed;
        top: 25%;
        left: 5%;
        padding: 0;
        margin: 0;
        width: 90%;
    }

    .inputFieldContainer {
        position: fixed;
        margin-left: 5%;
        margin-right: 5%;
        max-width: 800px;
        width: 70%;
        left: 7%;
        bottom: 2%;
    }

    .quickReplyContainer {
        position: absolute;
        left: 15%;
        bottom: 15%;
        width: 70%;
        margin: 0 auto;

    }

    .quickReply {
        font-size: 14px;
        line-height: 20px;
        height: 20px;
        margin: 6px;
        border-radius: 10px;
        margin-top: 20px;
        padding: 6px 10px;
    }
}

